import "./App.css";
import { MainAppBar } from "./view/MainAppBar";
import { HashRouter, Route, Routes } from "react-router-dom";
import { FeedPage } from "./page/FeedPage";
import { FeedDetailPage } from "./page/FeedDetailPage";
import { SignInPage } from "./page/SignInPage";
import { SignUpPage } from "./page/SignUpPage";
import { createContext, useEffect, useState } from "react";
import { getCurrentUser } from "./data/UserData";
import { SettingsPage } from "./page/SettingsPage";
import { ProfilePage } from "./page/ProfilePage";
import { EditorPage } from "./page/EditorPage";
import { ProtectedRoute } from "./utils/ProtectedRoute";

export const UserContext = createContext(null);

function App() {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    setLoading(true);
    if (localStorage.getItem("user")) {
      setLoading(true);
      getCurrentUser(localStorage.getItem("user")).then((value) => {
        setUser(value.user);
        setLoading(false);
      });
    } else {
      setLoading(false);
    }
  }, []);

  return (
    <UserContext.Provider value={{ user, setUser }}>
      {loading ? (
        <div />
      ) : (
        <HashRouter>
          <MainAppBar />
          <Routes>
            <Route path={"/"} element={<FeedPage />} />
            <Route path={"/article/:slug"} element={<FeedDetailPage />} />
            <Route path={"/login"} element={
              <ProtectedRoute requireLoggedIn={false} component={<SignInPage />} />
            } />
            <Route path={"/register"} element={
              <ProtectedRoute requireLoggedIn={false} component={<SignUpPage />} />
            } />
            <Route path={"/settings"} element={
              <ProtectedRoute requireLoggedIn={true} component={<SettingsPage />} />
            }
            />
            <Route path={"/@:username"} element={<ProfilePage />} />
            <Route exact={false} path={"/editor"} element={
              <ProtectedRoute requireLoggedIn={true} component={<EditorPage />} />
            }
            />
            <Route path={"/editor/:slug"} element={
              <ProtectedRoute requireLoggedIn={true} component={<EditorPage />} />
            }
            />

          </Routes>
        </HashRouter>
      )}
    </UserContext.Provider>
  );
}

export default App;
