import { Navigate, Route } from "react-router-dom";

export function ProtectedRoute({ component, requireLoggedIn, ...restOfProps }) {
  const isAuthenticated = localStorage.getItem("isAuthenticated");
  console.log("this", isAuthenticated);

  return (
    requireLoggedIn ?
      localStorage.getItem("user") ? component : <Navigate to={"/"} />:
      !localStorage.getItem("user") ? component : <Navigate to={"/"} />
  );
}